@import url('slider/swiper-bundle.css')
@import url('authRegForm/reg.css')
@import url('authRegForm/auth.css')

$bg-main-color: #EDE1DE

@font-face
    font-family: 'Raleway'
    src: url('../fonts/Raleway/static/Raleway-Regular.ttf')

@font-face
    font-family: 'Roboto'
    src: url('../fonts/Roboto/Roboto-Regular.ttf')

*,
*:before,
*:after
    margin: 0
    padding: 0
    box-sizing: border-box
    font-weight: normal

ol,
ul
    list-style: none

a
    text-decoration: none

input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration
    display: none

body
    font-family: 'Raleway',
    font-style: normal

body::-webkit-scrollbar
    width: 10px
    background-color: #f9f9fd

body::-webkit-scrollbar-thumb
    border-radius: 10px
    background-color: #87957E

body::-webkit-scrollbar-track
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2)
    border-radius: 10px
    background-color: #f9f9fd

button
    width: 250px
    height: 50px
    background: #87957E
    border: 1px solid #3A4736
    border-radius: 6px
    margin-top: 49px
    font-weight: 400
    font-size: 20px
    line-height: 20px
    color: #EEE4E2
    cursor: pointer
    position: relative
    z-index: 1
    overflow: hidden

button:hover
    color: #87957E

.title
    font-weight: 400
    font-size: 32px
    line-height: 20px
    color: #3A4736

.container
    max-width: 1210px
    margin: 0 auto
.header-main
    background-color: $bg-main-color
    min-width: 320px // 1140px ?
    position: sticky
    top: 0
    z-index: 100
    width: 100%
    // border: 1px solid #87957E
    // border-radius: 6px
    .header__container
        display: flex
        align-items: center
        justify-content: space-between

        .header__left-block
            display: flex
            align-items: center
            .main-logo__img
                width: 111px
                height: 51px
                padding: 3px 0px 0px

            .left-block__nav-head
                margin-left: 69px

                .nav-head__ul
                    display: flex
                    .nav-head__li:nth-child(1)
                        margin-right: 40px
                    .nav-head__li:nth-child(2)
                        margin-right: 40px
                    .nav-head__li:nth-child(3)
                        margin-right: 40px
                    .nav-head__btn
                        color: #3A4736
                        font-weight: 400
                        font-size: 20px
                        line-height: 20px
                        transition: box-shadow 0.1s ease-out
                        padding: 4px
                        border-radius: 6px
                        &:hover
                            box-shadow: 0 0 0 1px #87957E
                            color: #3a4736a7
        .header__right-block
            display: flex
            align-items: center

            .right-block__search-head
                display: flex
                align-items: center
                position: relative
                margin-right: 20px

                .search-head__input
                    border: 1px solid #afaf9ea0
                    border-radius: 6px
                    background-color: $bg-main-color
                    width: 176px
                    height: 40px
                    padding: 8px
                    font-weight: 400
                    font-size: 16px
                    line-height: 24px
                    position: absolute
                    right: 0px
                    z-index: 12
                    transition: 0.7s
                    &::placeholder
                        font-weight: 400
                        font-size: 16px
                        line-height: 20px
                    &:focus
                        width: 380px

                .search-head__btn
                    position: absolute
                    right: 11px
                    z-index: 13
                    .btn-head__img
                        fill: #87957E
                        &:hover
                            fill: black

            .right-block__btn-head
                display: flex
                align-items: center

                .btn-head__heart
                .heart__btn
                    margin-right: 22px
                    width: 28px
                    height: 23px
                    fill: none
                    &:hover
                        cursor: pointer
                        fill: #87957E

                .person__btn
                    width: 28px
                    height: 23px
                    fill: none
                    stroke: black
                    &:hover
                        cursor: pointer
                        fill: #87957E
                        stroke: #87957E

.header-slider
    background-color: $bg-main-color
    .swiper
        padding-bottom: 40px
        width: 100%
        height: 100%
    .swiper-wrapper
        position: relative
        max-height: 100%
        .swiper-slide
            position: relative
        .swiper-slide__img
            width: 100%
            height: 500px
            object-fit: cover
        .container
            max-width: 1000px
            margin: 0 auto
            .slider__call
                position: absolute
                top: 0px
                .slide__text
                    font-weight: 400
                    font-size: 24px
                    line-height: 23px
                    color: #3A4736
                    margin-top: 18px
                .call__title
                    font-weight: 500
                    font-size: 40px
                    line-height: 45px
                    text-indent: 3px
                    color: #3A4736
                    margin-top: 80px
                .call__slide-2
                    margin-top: 77px
                button
                    width: 250px
                    height: 50px
                    background: #87957E
                    border: 1px solid #3A4736
                    border-radius: 6px
                    margin-top: 49px
                    font-weight: 400
                    font-size: 20px
                    line-height: 20px
                    color: #EEE4E2
                    cursor: pointer
                    position: relative
                    z-index: 1
                    overflow: hidden
                    transition: all 0.5s ease-out
                button:hover
                    color: #87957E
                button::after
                    content: ""
                    background: #EEE4E2
                    position: absolute
                    z-index: -1
                    padding: 0.85em 0.75em
                    display: block
                button[class^="call"]::after
                    transition: all 0.6s ease-out
                button[class^="call"]:hover::after
                    transition: all 0.6s ease-out
                button.call__btn::after
                    border-radius: 50%
                    left: -30%
                    right: -30%
                    top: -150%
                    bottom: -150%
                    line-height: 8.34em
                    transform: scale(0, 0.4)
                    opacity: 0.5
                button.call__btn:hover::after
                    transform: scale(1, 1)
                    opacity: 1

    .swiper-pagination
        left: 0
        display: flex
        justify-content: center
        align-items: center
        .swiper-pagination-bullet
            background: #87957E
            width: 12px
            height: 12px
            border-radius: 85.8369px
        .swiper-pagination-bullet-active
            width: 53px !important
            height: 19px !important
            background: #87957E
            border: none
            border-radius: 85.8369px

//  Interior  ////////////////

.interior
    background-color: $bg-main-color
    min-width: 320px
    // padding-bottom: 57px
    // border: 1px solid #3A4736
    // border-radius: 6px 6px 6px 6px
    // border-top: none

    .interior__title
        margin-bottom: 30px
        padding-top: 34px

    .interior__cards
        display: grid
        grid-template-columns: 1fr 1fr 1fr
        grid-template-rows: 320px 320px
        gap: 28px 30px
        grid-template-areas: "Area1 Area2 Area3" "Area4 Area4 Area3"
        // padding-bottom: 60px

        .img-div
            width: 100%
            height: 100%

            .interior__img
                width: 100%
                height: 100%
                object-fit: cover
                border: 1px solid #87957E
                border-bottom: none
                border-radius: 6px 6px 0px 0px

        .interior__caption
            background-color: white
            display: flex
            justify-content: center
            align-items: center
            background: #FDFDFD
            border: 1px solid #87957E
            border-top: none
            border-radius: 0px 0px 6px 6px
            height: 50px
            .interior__text
                font-weight: 400
                font-size: 20px
                line-height: 20px
                color: #3A4736

        .interior__card:nth-child(1)
            grid-area: Area1
            margin-bottom: 50px

        .interior__card:nth-child(4)
            grid-area: Area4
            margin-bottom: 50px

        .interior__card:nth-child(2)
            grid-area: Area2
            margin-bottom: 50px

        .interior__card:nth-child(3)
            grid-area: Area3
            margin-bottom: 50px
            div:nth-child(2)
                background-color: white
                display: flex
                justify-content: center
                align-items: center
                background: #FDFDFD
                border: 1px solid #87957E
                border-top: none
                border-radius: 0px 0px 6px 6px
                height: 50px
                cursor: pointer
                position: relative
                z-index: 1
                overflow: hidden
                transition: all 0.5s ease-out
                font-weight: 400
                font-size: 20px
                line-height: 20px
                color: #3A4736
            div:hover
                color: #EDE1DE
            div::after
                content: ""
                background: #87957E
                position: absolute
                z-index: -1
                padding: 0.85em 0.75em
                display: block
            div[class^="interior__text-animation"]::after
                transition: all 0.6s ease-out
            div[class^="interior__text-animation"]:hover::after
                transition: all 0.6s ease-out
            div.interior__text-animation::after
                border-radius: 50%
                left: -30%
                right: -30%
                top: -150%
                bottom: -150%
                line-height: 8.34em
                transform: scale(0, 0.4)
                opacity: 0.5
            div.interior__text-animation:hover::after
                transform: scale(1, 1)
                opacity: 1

.articles
    background-color: $bg-main-color
    min-width: 320px
    // padding-top: 135px
    padding-bottom: 44px
    position: relative

    .articles__title
        padding-top: 60px
        // margin-bottom: 30px

    .swiper
        padding-right: 2px
        padding-left: 2px

    .swiper-slide
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25)
        border: 1px solid #87957E
        border-radius: 6px
        transition: .3s
        height: 404px
        overflow: hidden
        display: inline-block
        position: relative
        &:hover
            transform-origin: right
            transform: scale(1.04)
        .article-like
            width: 43px
            height: 41px
            background: rgba(217, 217, 217, 0.6)
            border-radius: 6px
            display: flex
            align-items: center
            justify-content: center
            position: absolute
            top: 11px
            right: 12px
        .article-like > svg
            stroke: #3A4736
            transition: all 0.4s
            &:hover
                stroke: #DD4646

    .swiper-slide-active:hover
        transform-origin: 0% 50%
        transform: scale(1.04)

    .swiper-slide-next:hover
        transform-origin: 0% 50%
        transform: scale(1.04)

    .article-title_div
        display: flex
        justify-content: center
        height: 104px

        .article-title
            max-width: 240px
            font-weight: 400
            max-height: 60px
            font-size: 16px
            line-height: 20px
            color: #3A4736
            text-align: center
            -webkit-line-clamp: 3
            display: -webkit-box
            -webkit-box-orient: vertical
            overflow: hidden

    .swiper-slide img
        height: 300px
        width: 100%
        object-fit: cover
        border-radius: 6px 6px 0px 0px
        margin-bottom: 20px
        border-bottom: 1px solid #87957E

    .swiper-pagination
        // display: flex
        // justify-content: center
        position: inherit !important
        // width: 100% !important
        padding-top: 40px

        .swiper-pagination-bullet
            background: #87957E
            width: 19px !important
            height: 19px !important
            border: 2px solid #3A4736
            border-radius: 85.8369px
        .swiper-pagination-bullet-active
            width: 53px !important
            height: 19px !important
            background: #87957E
            border: none
            border-radius: 85.8369px
    .articles__background
        width: 100%
        min-height: 113px
        background: #87957E
        border: 1px solid #3A4736
        border-radius: 6px
        position: absolute
        top: 257px

    .articles__showMore
        display: flex
        align-items: center
        justify-content: right
        .articles__btn
            font-weight: 400
            font-size: 20px
            color: #3a473693
            padding-right: 3px
            &::after
                display: block
                content: ''
                border-bottom: solid 2px #3a4736b5
                transform: scaleX(0)
                transition: transform 0.2s ease-in-out
            &:hover::after
                transform: scaleX(1)

            &::after
                transform-origin: 0% 50%
            &:hover
                color: #3A4736
                font-weight: 500

