.main-page__footer
    min-width: 320px
    background: #525D4D
    border-top: 1px solid #3A4736
    //border-radius: 6px
    .footer__container
        display: flex
        justify-content: space-between
        align-items: center
        padding-top: 67px
        padding-bottom: 52px
        .footer__logo
            margin-right: 8px
            padding-top: 10px
        .footer__nav
            display: flex
            ul:nth-child(1)
                margin-right: 30px
            ul:nth-child(2)
